export default "<template>\n  <div>\n    <h1>Inputs</h1>\n\n    <div class=\"section\">\n      <h2>Text Inputs</h2>\n      <p>\n        These 6 fields below are wrapped in a\n        <code>FormGroup</code> component. This will put 16px of margin between\n        form fields and 24px margin between form groups.\n      </p>\n\n      <pre><code>import { TextInput, TextArea, VariableMenu } from 'streamlabs-beaker';\n\ncomponents: {\n  TextInput,\n  TextArea,\n  VariableMenu\n}</code></pre>\n\n      <DemoSection title=\"Text Inputs\" :code=\"demoCode\">\n        <template #components>\n          <FormGroup>\n            <TextInput\n              slot=\"input\"\n              type=\"text\"\n              label=\"Text Input\"\n              v-model=\"textInputValue\"\n              name=\"textExample\"\n              :placeholder=\"textInputPlaceholder\"\n              autoComplete=\"on\"\n            />\n\n            <TextInput\n              slot=\"input\"\n              type=\"number\"\n              label=\"Number Input\"\n              v-model=\"numberInputValue\"\n              name=\"numberinputExample\"\n              :placeholder=\"textInputPlaceholder\"\n              :min=\"0\"\n              :max=\"100\"\n              v-validate=\"'required|between:0,100'\"\n              :error=\"errors.first('numberinputExample')\"\n            />\n\n            <TextInput\n              slot=\"input\"\n              type=\"email\"\n              label=\"Email Input\"\n              v-model=\"emailInputValue\"\n              name=\"emailExample\"\n              :placeholder=\"emailInputPlaceholder\"\n            />\n\n            <TextInput\n              slot=\"input\"\n              type=\"password\"\n              label=\"Password Input\"\n              v-model=\"passwordInputValue\"\n              name=\"passwordExample\"\n              :placeholder=\"passwordInputPlaceholder\"\n              disabled\n            />\n\n            <TextInput\n              slot=\"input\"\n              type=\"text\"\n              label=\"Input With Error\"\n              v-model=\"errorTextInputValue\"\n              name=\"textExample\"\n              :placeholder=\"textInputPlaceholder\"\n              :error=\"'Enter a number'\"\n            />\n\n            <TextArea\n              slot=\"input\"\n              label=\"Text Area\"\n              v-model=\"textAreaInputValue\"\n              name=\"myarea\"\n              placeholder=\"This is where you put some cool stuff\"\n              autoResize=\"true\"\n              :maxLength=\"1000\"\n              :maxHeight=\"100\"\n            />\n          </FormGroup>\n        </template>\n      </DemoSection>\n    </div>\n\n    <div class=\"section\">\n      <h2>Text Inputs with Variable Menu Wrapper</h2>\n      <p>\n        A new way to navigate variables, just wrap your input with the Variable\n        Menu. This is not work with FormGroup preformatting.\n      </p>\n\n      <DemoSection title=\"Variable Menu\" :code=\"demoCode\">\n        <template #components>\n          <VariableMenu :jsonSearch=\"varData\">\n            <text-input\n              slot=\"input\"\n              type=\"text\"\n              v-model=\"variTextValue\"\n              name=\"textExample\"\n              :placeholder=\"'w/ Variable Menu'\"\n            />\n          </VariableMenu>\n\n          <VariableMenu :jsonSearch=\"varData\">\n            <TextArea\n              slot=\"input\"\n              ref=\"textArea\"\n              v-model=\"variAreaValue\"\n              name=\"myarea\"\n              placeholder=\"w/ Variable Menu\"\n              rows=\"3\"\n              :maxLength=\"1000\"\n              :maxHeight=\"100\"\n            />\n          </VariableMenu>\n        </template>\n      </DemoSection>\n\n      <table class=\"docs-table\">\n        <thead>\n          <tr>\n            <th>Prop</th>\n            <th>Type</th>\n            <th>Default</th>\n            <th>Description</th>\n          </tr>\n        </thead>\n        <tbody>\n          <tr>\n            <td>disabled</td>\n            <td>boolean</td>\n            <td>null</td>\n            <td>\n              Puts a disabled class on the form field and disables the input.\n            </td>\n          </tr>\n          <tr>\n            <td>error</td>\n            <td>string</td>\n            <td>null</td>\n            <td>\n              If there is error text, error classes will go on the input - we\n              plan to add better validation handling.\n            </td>\n          </tr>\n          <tr>\n            <td>label</td>\n            <td>string</td>\n            <td>null</td>\n            <td>Optional label for the input.</td>\n          </tr>\n          <tr>\n            <td>name</td>\n            <td>string</td>\n            <td>null</td>\n            <td>Input name attribute.</td>\n          </tr>\n          <tr>\n            <td>placeholder</td>\n            <td>string</td>\n            <td>null</td>\n            <td>Input placeholder text.</td>\n          </tr>\n          <tr>\n            <td>type</td>\n            <td>string</td>\n            <td>text</td>\n            <td>\n              Input type:\n              <code>text</code>, <code>email</code>, <code>password</code>.\n            </td>\n          </tr>\n          <tr>\n            <td>value</td>\n            <td>string</td>\n            <td>null</td>\n            <td>Input value using v-model.</td>\n          </tr>\n          <tr>\n            <td>readonly</td>\n            <td>boolean</td>\n            <td>null</td>\n            <td>Specifies that an input field is read-only.</td>\n          </tr>\n        </tbody>\n      </table>\n    </div>\n\n    <div class=\"section\">\n      <h2>Tagging Input</h2>\n      <pre><code>import { TaggingInput } from 'streamlabs-beaker';\n\ncomponents: {\n  TaggingInput\n}</code></pre>\n\n      <DemoSection title=\"Tagging Input\" :code=\"demoCode\">\n        <template #components>\n          <TaggingInput\n            name=\"aliases\"\n            placeholder=\"!hello\"\n            maxItems=\"10\"\n            inputValidation=\"required\"\n            @keydown.space.prevent\n          />\n        </template>\n      </DemoSection>\n    </div>\n\n    <div class=\"section\">\n      <h2>Selector</h2>\n\n      <pre><code>import { Selector } from 'streamlabs-beaker';\n\ncomponents: {\n  Selector\n}</code></pre>\n\n      <DemoSection title=\"Selector\" :code=\"demoCode\">\n        <template #components>\n          <FormGroup>\n            <Selector\n              slot=\"input\"\n              v-model=\"selected\"\n              :options=\"['Option A', 'Option B', 'Option C']\"\n            />\n\n            <Selector\n              slot=\"input\"\n              v-model=\"selectedGroup\"\n              :options=\"optionGroups\"\n              group-values=\"items\"\n              group-label=\"group\"\n              :searchable=\"false\"\n            />\n\n            <Selector\n              slot=\"input\"\n              v-model=\"selected\"\n              :options=\"['Option A', 'Option B', 'Option C']\"\n              disabled\n            />\n\n            <Selector\n              slot=\"input\"\n              multiple\n              v-model=\"multipleSelected\"\n              :options=\"['Option A', 'Option B', 'Option C']\"\n              :searchable=\"false\"\n            />\n\n            <Selector\n              slot=\"input\"\n              multiple\n              v-model=\"multipleSelected\"\n              :options=\"['Option A', 'Option B', 'Option C']\"\n              disabled\n            />\n          </FormGroup>\n        </template>\n      </DemoSection>\n\n      <table class=\"docs-table\">\n        <thead>\n          <tr>\n            <th>Prop</th>\n            <th>Type</th>\n            <th>Default</th>\n            <th>Description</th>\n          </tr>\n        </thead>\n        <tbody>\n          <tr>\n            <td>value</td>\n            <td>string</td>\n            <td>null</td>\n            <td>Current selected value.</td>\n          </tr>\n          <tr>\n            <td>options</td>\n            <td>[''] or {}</td>\n            <td>null</td>\n            <td>Array of strings or objects to use as the options.</td>\n          </tr>\n          <tr>\n            <td>multiple</td>\n            <td>Boolean</td>\n            <td>false</td>\n            <td>Optional label for the input.</td>\n          </tr>\n          <tr>\n            <td>placeholder</td>\n            <td>String</td>\n            <td>''</td>\n            <td>Equivalent to the `placeholder` attribute on an input.</td>\n          </tr>\n          <tr>\n            <td>searchable</td>\n            <td>Boolean</td>\n            <td>true</td>\n            <td>Optional label for the input.</td>\n          </tr>\n          <tr>\n            <td>disabled</td>\n            <td>Boolean</td>\n            <td>false</td>\n            <td>\n              Puts a disabled class on the form field and disables the input.\n            </td>\n          </tr>\n        </tbody>\n      </table>\n    </div>\n\n    <div class=\"section\">\n      <h2>Checkboxes and Radios</h2>\n      <pre><code>import { Checkbox, Radio } from 'streamlabs-beaker';\n\ncomponents: {\n  Checkbox,\n  Radio\n}</code></pre>\n\n      <div class=\"section\">\n        <h2>Single Checkbox</h2>\n\n        <DemoSection title=\"Checkbox\" :code=\"demoCode\">\n          <template #components>\n            <Checkbox\n              v-model=\"checkboxValue1\"\n              id=\"checkbox\"\n              name=\"checkbox\"\n              label=\"Checkbox Label\"\n            />\n          </template>\n        </DemoSection>\n      </div>\n\n      <div class=\"section\">\n        <h2>Checkbox Group</h2>\n\n        <DemoSection title=\"Checkbox Group\" :code=\"demoCode\">\n          <template #components>\n            <div class=\"s-checkbox-group\">\n              <Checkbox\n                v-model=\"checkboxValue2\"\n                id=\"checkbox1\"\n                name=\"checkbox1\"\n                label=\"Checkbox Label 1\"\n              />\n              <Checkbox\n                v-model=\"checkboxValue3\"\n                id=\"checkbox2\"\n                name=\"checkbox2\"\n                label=\"Checkbox Label 2\"\n              />\n              <Checkbox\n                v-model=\"checkboxValue4\"\n                id=\"checkbox3\"\n                name=\"checkbox3\"\n                label=\"Checkbox Label 3\"\n              />\n            </div>\n          </template>\n        </DemoSection>\n      </div>\n    </div>\n\n    <div class=\"section\">\n      <h2>Radios</h2>\n\n      <DemoSection title=\"Radios\" :code=\"demoCode\">\n        <template #components>\n          <div class=\"s-checkbox-group\">\n            <Radio\n              v-model=\"radioValue\"\n              id=\"radio1\"\n              name=\"radioGroup\"\n              label=\"Enabled\"\n              :val=\"true\"\n            />\n            <Radio\n              v-model=\"radioValue\"\n              id=\"radio2\"\n              name=\"radioGroup\"\n              label=\"Disabled\"\n              :val=\"false\"\n            />\n          </div>\n        </template>\n      </DemoSection>\n\n      <table class=\"docs-table\">\n        <thead>\n          <tr>\n            <th>Prop</th>\n            <th>Type</th>\n            <th>Default</th>\n            <th>Description</th>\n          </tr>\n        </thead>\n        <tbody>\n          <tr>\n            <td>id</td>\n            <td>string</td>\n            <td>\"\"</td>\n            <td>Indentifier for associated label.</td>\n          </tr>\n          <tr>\n            <td>name</td>\n            <td>string</td>\n            <td>\"\"</td>\n            <td>Name for radio group.</td>\n          </tr>\n          <tr>\n            <td>label</td>\n            <td>string</td>\n            <td>null</td>\n            <td>Optional label for the radio input.</td>\n          </tr>\n          <tr>\n            <td>val</td>\n            <td>string, boolean</td>\n            <td>\"\"</td>\n            <td>Radio input value when selected</td>\n          </tr>\n        </tbody>\n      </table>\n    </div>\n\n    <div class=\"section\">\n      <h2>Status Switch</h2>\n      <pre><code>import { StatusSwitch } from 'streamlabs-beaker';\n\ncomponents: {\n  StatusSwitch\n}</code></pre>\n\n      <div class=\"section\">\n        <DemoSection title=\"Status Switch\" :code=\"demoCode\">\n          <template #components>\n            <FormGroup>\n              <StatusSwitch\n                slot=\"input\"\n                v-model=\"statusValue\"\n                label=\"Switch Label\"\n              />\n\n              <StatusSwitch\n                slot=\"input\"\n                size=\"small\"\n                v-model=\"statusValue\"\n                label=\"Small Switch Label\"\n              />\n            </FormGroup>\n          </template>\n        </DemoSection>\n      </div>\n\n      <table class=\"docs-table\">\n        <thead>\n          <tr>\n            <th>Prop</th>\n            <th>Type</th>\n            <th>Default</th>\n            <th>Description</th>\n          </tr>\n        </thead>\n        <tbody>\n          <tr>\n            <td>value</td>\n            <td>boolean</td>\n            <td>false</td>\n            <td>Available for v-model 2-way data binding.</td>\n          </tr>\n          <tr>\n            <td>label</td>\n            <td>string</td>\n            <td>null</td>\n            <td>Optional label for the input.</td>\n          </tr>\n          <tr>\n            <td>size</td>\n            <td>boolean</td>\n            <td>false</td>\n            <td>\n              Other available sizes. Current alternate size option:\n              <code>small</code>.\n            </td>\n          </tr>\n        </tbody>\n      </table>\n    </div>\n\n    <div class=\"section\">\n      <h2>Image Input Picker</h2>\n      <pre><code>import { ImagePickerInput } from 'streamlabs-beaker';\n\ncomponents: {\n  ImagePickerInput\n}</code></pre>\n\n      <div class=\"section\">\n        <h3>Default</h3>\n        <p>\n          By default, the image input picker is setup to have our Alert Box\n          layout styles as the options.\n        </p>\n\n        <DemoSection title=\"Default Image Input Picker\" :code=\"demoCode\">\n          <template #components>\n            <ImagePickerInput v-model=\"layoutValue\" />\n          </template>\n        </DemoSection>\n      </div>\n\n      <div class=\"section\">\n        <h3>Custom</h3>\n        <p>\n          You can pass in any options you'd like with the options prop. This is\n          an example using our Jar options.\n        </p>\n        <DemoSection title=\"Custom Image Input Picker\" :code=\"demoCode\">\n          <template #components>\n            <ImagePickerInput v-model=\"jarValue\" :options=\"options\" />\n          </template>\n        </DemoSection>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script lang=\"ts\">\nimport { Component, Watch, Vue } from \"vue-property-decorator\";\n\nimport Checkbox from \"./../components/Checkbox.vue\";\nimport DemoSection from \"./../components/DemoSection.vue\";\nimport FormGroup from \"./../components/FormGroup.vue\";\nimport ImagePickerInput from \"./../components/ImagePickerInput.vue\";\nimport InputsCode from \"./Inputs.vue?raw\";\nimport Radio from \"./../components/Radio.vue\";\nimport Selector from \"./../components/Selector.vue\";\nimport StatusSwitch from \"./../components/StatusSwitch.vue\";\nimport TaggingInput from \"./../components/TaggingInput.vue\";\nimport TextArea from \"./../components/TextArea.vue\";\nimport TextInput from \"./../components/TextInput.vue\";\nimport varSearch from \"./../components/cloudbotvariables.json\";\nimport VariableMenu from \"./../components/VariableMenu.vue\";\n\n@Component({\n  components: {\n    Checkbox,\n    DemoSection,\n    FormGroup,\n    ImagePickerInput,\n    Radio,\n    Selector,\n    StatusSwitch,\n    TaggingInput,\n    TextArea,\n    TextInput,\n    VariableMenu\n  }\n})\nexport default class Inputs extends Vue {\n  demoCode = InputsCode;\n  data = \"\";\n\n  radioValue = true;\n\n  checkboxValue1 = true;\n  checkboxValue2 = false;\n  checkboxValue3 = true;\n  checkboxValue4 = false;\n  selected = \"Option A\";\n  selectedGroup = \"Option A\";\n  multipleSelected = [\"Option B\", \"Option C\"];\n  optionGroups = [\n    {\n      group: \"Group A\",\n      items: [\"Option A\", \"Option B\", \"Option C\"]\n    },\n    {\n      group: \"Group B\",\n      items: [\"Option A\", \"Option B\", \"Option C\"]\n    },\n    {\n      group: \"Group C\",\n      items: [\"Option A\", \"Option B\", \"Option C\"]\n    }\n  ];\n  optionSelected = [\"Glass Pint\", \"Glass Beer\"];\n  objectSelected = {\n    value: \"glass-pint\",\n    title: \"Glass Pint\",\n    image: \"https://cdn.streamlabs.com/static/tip-jar/jars/glass-pint.png\"\n  };\n  statusValue = true;\n  testingInput = null;\n\n  layoutValue = \"above\";\n  jarValue = \"glass-pint\";\n\n  // For Variable Menu\n  varData = varSearch;\n  variAreaValue = \"\";\n  variTextValue = \"\";\n\n  // Text inputs\n  textInputValue = \"test\";\n  numberInputValue = 0;\n  emailInputValue = \"\";\n  passwordInputValue = \"\";\n  errorTextInputValue = \"\";\n  textAreaInputValue = \"\";\n\n  textInputPlaceholder = \"Placeholder\";\n  emailInputPlaceholder = \"Placeholder\";\n  passwordInputPlaceholder = \"Placeholder\";\n\n  options = [\n    {\n      value: \"glass-pint\",\n      title: \"Glass Pint\",\n      image: \"https://cdn.streamlabs.com/static/tip-jar/jars/glass-pint.png\"\n    },\n    {\n      value: \"glass-beer\",\n      title: \"Glass Beer\",\n      image: \"https://cdn.streamlabs.com/static/tip-jar/jars/glass-beer.png\"\n    },\n    {\n      value: \"glass-beer-2\",\n      title: \"Glass Beer 2\",\n      image: \"https://cdn.streamlabs.com/static/tip-jar/jars/glass-beer2.png\"\n    },\n    {\n      value: \"glass-coffee\",\n      title: \"Glass Coffee\",\n      image: \"https://cdn.streamlabs.com/static/tip-jar/jars/glass-coffee.png\"\n    },\n    {\n      value: \"glass-fancy\",\n      title: \"Glass Fancy\",\n      image: \"https://cdn.streamlabs.com/static/tip-jar/jars/glass-fancy.png\"\n    },\n    {\n      value: \"glass-whiskey\",\n      title: \"Glass Whiskey\",\n      image: \"https://cdn.streamlabs.com/static/tip-jar/jars/glass-whiskey.png\"\n    },\n    {\n      value: \"glass-burbon\",\n      title: \"Glass Burbon\",\n      image: \"https://cdn.streamlabs.com/static/tip-jar/jars/glass-burbon.png\"\n    },\n    {\n      value: \"glass-martini\",\n      title: \"Glass Martini\",\n      image: \"https://cdn.streamlabs.com/static/tip-jar/jars/glass-martini.png\"\n    },\n    {\n      value: \"glass-beer-3\",\n      title: \"Glass Beer 3\",\n      image: \"https://cdn.streamlabs.com/static/tip-jar/jars/glass-beer3.png\"\n    },\n    {\n      value: \"glass-wine\",\n      title: \"Glass Wine\",\n      image: \"https://cdn.streamlabs.com/static/tip-jar/jars/glass-wine.png\"\n    },\n    {\n      value: \"glass-baileys\",\n      title: \"Glass Baileys\",\n      image: \"https://cdn.streamlabs.com/static/tip-jar/jars/glass-baileys.png\"\n    },\n    {\n      value: \"glass-champagne\",\n      title: \"Glass Champagne\",\n      image:\n        \"https://cdn.streamlabs.com/static/tip-jar/jars/glass-champagne.png\"\n    },\n    {\n      value: \"glass-coffee-no-handle\",\n      title: \"Glass Coffee No Handle\",\n      image:\n        \"https://cdn.streamlabs.com/static/tip-jar/jars/glass-coffee-no-handle.png\"\n    },\n    {\n      value: \"glass-plinko\",\n      title: \"Glass Plinko\",\n      image: \"https://cdn.streamlabs.com/static/tip-jar/jars/glass-plinko.png\"\n    },\n    {\n      value: \"glass-stocking\",\n      title: \"Glass Stocking\",\n      image: \"https://cdn.streamlabs.com/static/tip-jar/jars/glass-stocking.png\"\n    },\n    {\n      value: \"glass-snowman\",\n      title: \"Glass Snowman\",\n      image: \"https://cdn.streamlabs.com/static/tip-jar/jars/glass-snowman.png\"\n    }\n  ];\n\n  show = [\n    \"Bounce\",\n    \"Bounce In\",\n    \"Bounce In Down\",\n    \"Bounce In Left\",\n    \"Bounce In Right\",\n    \"Bounce In Up\",\n    \"Fade In\",\n    \"Fade In Down\",\n    \"Fade In Down Big\",\n    \"Fade In Left\",\n    \"Fade In Left Big\",\n    \"Fade In Right\",\n    \"Fade In\",\n    \"Fade In Up\",\n    \"Fade In Up Big\"\n  ];\n}\n</script>\n"